import React, {useState} from "react";
import {BsInfoCircleFill} from "react-icons/bs";
import NumberFormat from "react-number-format";


const BusinessFinances = (props) => {
    const localization = {style:'currency', currency: 'ZAR'};
    const displayAsCurrency = (value) => new Intl.NumberFormat('en-ZA', localization).format(value);

    const styles = {
        sectionMargin: {
            marginTop:40
        },
        removeElement: {
            display:"none",
        },
    }

    return(
        <div className="input-section">
            <h3 className="step-heading">Business Financials</h3>
            <br/>
            <div style={{width:"80%"}}>
                {/* annual sales */}
                <div className="application-form inline-input-elements">
                    <div><h5 className="step-heading">What is your expected annual sales figure in Rs?</h5></div>
                </div>
                <NumberFormat
                    className="team-size-input registration-number-input"
                    isNumericString={true}
                    name="annual-sales"
                    // format="### ### ####"
                    placeholder="Expected annual sales"
                    onValueChange={(values)=>{
                        const {formattedValue, value} = values;
                        props.setAnnualSales(value);
                    }}
                    value={props.annualSales}
                    required
                />
                <p className="errorMessage" style={(props.errors.annualSalesError)?null:styles.removeElement}>
                    {props.errors.annualSalesError}<BsInfoCircleFill className="infoBtn"/>
                </p>
                
                {/* annual cost of sales */}
                <div className="application-form inline-input-elements" style={styles.sectionMargin}>
                    <div><h5 className="step-heading">What is your expected annual cost of sales figure in Rs?</h5></div>
                </div>
                <NumberFormat
                    className="team-size-input registration-number-input"
                    isNumericString={true}
                    name="annual-sales"
                    // format="### ### ####"
                    placeholder="Expected annual cost of sales"
                    onValueChange={(values)=>{
                        const {formattedValue, value} = values;
                        props.setAnnualCostOfSales(value);
                    }}
                    value={props.annualCostOfSales}
                    required
                />
                <p className="errorMessage" style={(props.errors.annualCostOfSalesError)?null:styles.removeElement}>
                    {props.errors.annualCostOfSalesError}<BsInfoCircleFill className="infoBtn"/>
                </p>
                {/* annual expense */}
                <div className="application-form inline-input-elements" style={styles.sectionMargin}>
                    <div><h5 className="step-heading">What is your expected annual expenses figure in Rs?</h5></div>
                </div>
                <NumberFormat
                    className="team-size-input registration-number-input"
                    isNumericString={true}
                    name="annual-sales"
                    // format="### ### ####"
                    placeholder="Expected annual expenses"
                    onValueChange={(values)=>{
                        const {formattedValue, value} = values;
                        props.setAnnualExpenses(value);
                    }}
                    value={props.annualExpenses}
                    required
                />
                <p className="errorMessage" style={(props.errors.annualExpensesError)?null:styles.removeElement}>
                    {props.errors.annualExpensesError}<BsInfoCircleFill className="infoBtn"/>
                </p>
            </div>
        </div>
    );
}

export default BusinessFinances;