import React, { useState,useEffect } from 'react';
import Courses from './LearnV2Content/Courses';
import PurchasedCoursesComponent from './LearnV2Content/PurchasedCoursesComponent';
import CourseContent from './LearnV2Content/CourseContent';
import {authentication} from '../firebase/firebase';
import { usePaystackPayment } from 'react-paystack';
import Loader from "react-loader-spinner";
import Popup from "reactjs-popup";
const Learn = (props)=>{
    const [displayPopUp,setDisplayPopUp] = useState(false);
    const [courses,setCourses] = useState();
    const [selectedCourse,setSelectedCourse] = useState();
    const [purchasedCourses,setPurchasedCourses]= useState([]);
    // will store the ids of all purchased courses
    const [purchasedCourseIds,setPurchasedCourseIds]= useState([]);

    // configuration settings for the payment portal
    const config = {
        reference: (new Date()).getTime(),
        email: authentication.currentUser.email,
        amount: selectedCourse == undefined?0:selectedCourse.price*100,
        publicKey: 'pk_test_678a61b3b10c85cf881854823605d8c7a7aa7528',
        currency:'ZAR'
    };

    // initialise paystack
    const initializePayment = usePaystackPayment(config);

    // currency display configurations
    let localization = {style:'currency', currency: 'ZAR'};
    // method to display currency data
    let displayAsCurrency = (value) => new Intl.NumberFormat('en-ZA', localization).format(value);
    useEffect(()=>{
        getCourses();
    },[])

    // get list of available course
    const getCourses = ()=>{
        fetch(`https://mutualism-test.herokuapp.com/api/getCourses?userId=${authentication.currentUser.uid}`)
        .then((re)=>re.json())
        .then((re)=>{
            // set courses array and select the default course
            setCourses(re.data)
            setSelectedCourse(re.data[0]);
            setPurchasedCourseIds(re.purchased_course_ids);
            // setPurchasedCourses(re.)
            setPurchasedCourses(re.purchased_courses);
        });
    }

    const onSuccess = (reference)=>{
        // if paymnet successful
        if(reference.status == "success"){
            var purchased_course_id = selectedCourse.course_id;
            var user_id = authentication.currentUser.uid;
            setDisplayPopUp(true);
            fetch(`https://mutualism-test.herokuapp.com/api/buyCourse`,{
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body:"userId="+user_id+"&courseId="+purchased_course_id
            })
            .then(re=>re.json())
            .then(re=>{
                console.log(re);
                setDisplayPopUp(false);
                setCourses(undefined);
                setSelectedCourse(undefined);
                setPurchasedCourses([]);
                getCourses();
            })
            
        }
    }

    const onClose = ()=>{

    }

    return(
        <>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-xl-3 .col-12 left-learn-col">
                        <div className="left-side-bar">
                            <Courses courses={courses} selectCourse={setSelectedCourse}/>
                        </div>
                    </div>
                    <div className="col-xl-6 col-12">
                        {/* display course content once the course has loaded */}
                        {selectedCourse !== undefined? <CourseContent purchasedCourseIds={purchasedCourseIds} purchasedCourses={purchasedCourses} selectedCourse={selectedCourse}/>:null}
                    </div>
                    <div className="col-xl-3 col-12 right-col">
                        <div className="course-price">
                            {/* change heading based on whether the course has been purchased or not */}
                            {selectedCourse == undefined?
                                null
                                :
                                purchasedCourseIds.includes(selectedCourse.course_id)?
                                <p>You bought this course for</p>
                                :
                                <p>Buy this course</p>
                            }
                            <p className="price">{selectedCourse !== undefined?displayAsCurrency(selectedCourse.price): displayAsCurrency(0)}</p>
                            
                            {/* show buy button only if course was not purchased */}
                            {
                                selectedCourse == undefined?
                                null
                                :
                                purchasedCourseIds.includes(selectedCourse.course_id)?
                                null
                                :
                                <button onClick={()=>initializePayment(onSuccess, onClose)} style={{width:"30%", marginBottom:"20px",marginLeft:"30px",height:'30px'}} type="submit" className="view-qui-btn buy-course-btn">Buy now</button>
                            }
                        </div>
                        <div className="number-of-quizzes">
                            <p id="quizCountHeader">Number of quizzes</p>
                            {/* display the number of quizes in the course */}
                            <p className="quizCount">{selectedCourse !== undefined? selectedCourse.quizzess.length + " quiz/quizzes in this course": "0 quizzes."}</p>
                        </div>
                        <div className="completed-quizzes">
                            <p>Recently purchased courses</p>
                            <br/>
                            <PurchasedCoursesComponent purchasedCourses={purchasedCourses}/>
                        </div>  
                    </div>
                </div>
            </div>
            {/* who loader after making payment */}
            <Popup modal position="right center" open={displayPopUp} closeOnDocumentClick={false}>
                <Loader type="Grid" color="#212529" height={100} width={100}/>
            </Popup>
        </>


    )
}

export default Learn