import IndustryType from "./IndustryType";
import TeamSize from "./TeamSize";
import BusinessDescription from "./BusinessDescription";
import BusinessRegistrationProof from "./BusinessRegistrationProof";
import BusinessFinances from "./BusinessFinances";
import DebtHistory from "./DebtHistory";
import React, {useEffect, useState} from "react";
import _ from "lodash";

const isEmpty = (value) => {
    return ((value.trim() === null) || (value.trim() === ''));
}

const Content = (props) => {

    const [annualSales, setAnnualSales] = useState(()=>{
        const annualSales = localStorage.getItem('annualSales');
        return annualSales !== null? JSON.parse(annualSales):0
    });
    const [annualCostOfSales, setAnnualCostOfSales] = useState(()=>{
        const annualCostOfSales = localStorage.getItem('annualCostOfSales');
        return annualCostOfSales !== null? JSON.parse(annualCostOfSales):0
    });
    const [annualExpenses, setAnnualExpenses] = useState(()=>{
        const annualExpenses = localStorage.getItem('annualExpenses');
        return annualExpenses !== null? JSON.parse(annualExpenses):0
    });
    
    const [industryTypeState, setIndustryTypeState] = useState(()=>{
            const industry = localStorage.getItem('industryType');
            return industry !== null
                ? JSON.parse(industry)
                :{
                    value: "industry",
                    label: "Choose industry sector..."
                };
        }
    );

    const [teamSize, setTeamSize] = useState(()=>{
        const teamSize = localStorage.getItem('teamSize');
        return teamSize !== null || !isNaN(Number(teamSize))
            ? Number(teamSize)
            : 1 ;
    });

    const [summary, setSummary] = useState(()=>{
        const businessDescription = localStorage.getItem('businessDescription');
        return businessDescription !== null
            ? businessDescription
            : '';
    });
    const [cipcReg, setCipcReg] = useState(()=>{
        const businessRegistrationProofCipc = localStorage.getItem('businessRegistrationProofCipc');
        return businessRegistrationProofCipc !== null
            ? businessRegistrationProofCipc
            : '';
    });
    const [sarsReg, setSarsReg] = useState(()=>{
        const businessRegistrationProofSars = localStorage.getItem('businessRegistrationProofSars');
        return businessRegistrationProofSars !== null
            ? businessRegistrationProofSars
            : '';
    });
    
    const [hasDebtHistory, setHasDebtHistory] = useState(()=>{
        const hasDebtHistory = localStorage.getItem('hasDebtHistory');
        return hasDebtHistory!==null
            ? JSON.parse(hasDebtHistory)
            : false;
    });
    const [status, setStatus] = useState('');
    const [debtType, setDebtType] = useState(()=>{
        const typeOfDebt = localStorage.getItem('debtType');
        return (typeOfDebt!==null)?typeOfDebt:0;
    });


    const showBusinessRegistrations = ()=>{
        var stage_of_business = Number(localStorage.getItem('stageOfBusiness'));
        return stage_of_business === 10 || stage_of_business === 20? null
        :  
        <BusinessRegistrationProof
            show={true}
            cipc={cipcReg}
            setCipc={setCipcReg}
            sars={sarsReg}
            setSars={setSarsReg}
            errors={{
                cipcInputNoText: props.errors.cipcInputNoText,
                sarsInputNoText: props.errors.sarsInputNoText,
                cipcInputInvalid: props.errors.cipcInputInvalid,
                sarsInputInvalid: props.errors.sarsInputInvalid
            }}
        />
    }

    useEffect(()=>{
        localStorage.setItem('industryType', JSON.stringify(industryTypeState));
        localStorage.setItem('teamSize', teamSize.toString());
        localStorage.setItem('businessDescription', summary);
        localStorage.setItem('businessRegistrationProofCipc', cipcReg);
        localStorage.setItem('businessRegistrationProofSars', sarsReg);
        localStorage.setItem('hasDebtHistory', hasDebtHistory.toString());
        localStorage.setItem('debtType', debtType);
        localStorage.setItem('annualSales', annualSales.toString());
        localStorage.setItem('annualCostOfSales', annualCostOfSales.toString());
        localStorage.setItem('annualExpenses', annualExpenses.toString());
    },[
        industryTypeState,
        teamSize,
        summary,
        cipcReg,
        sarsReg,
        status,
        hasDebtHistory,
        debtType,
        annualSales,
        annualCostOfSales,
        annualExpenses
    ]);

    switch(props.businessDetailsStage){
        case 1: return (
            <div className="applicationInfo application-form">
                <IndustryType
                    show={true}
                    industryType={industryTypeState}
                    setIndustryType={setIndustryTypeState}
                    errors={props.errors.industryInputNoSelect?props.errors.industryInputNoSelect:''}
                />
                <TeamSize
                    show={industryTypeState.value!=="industry"}
                    teamSize={teamSize}
                    setTeamSize={setTeamSize}
                    errors={props.errors.teamSizeNoInput?props.errors.teamSizeNoInput:''}
                />
                <BusinessDescription
                    show={teamSize!==0}
                    summary={summary}
                    setSummary={setSummary}
                    errors={{
                        businessDescriptionNoText: props.errors.businessDescriptionNoText,
                        businessDescriptionTextTooShort: props.errors.businessDescriptionTextTooShort
                    }}
                />
            </div>
        );
        case 2:
            return(
            <div className="applicationInfo application-form">
                {/* if the business is in ideation or is newly operational there's no need for CIPC and SARS numbers */}
                {showBusinessRegistrations()}
                <BusinessFinances
                    setAnnualSales={setAnnualSales}
                    setAnnualCostOfSales={setAnnualCostOfSales}
                    setAnnualExpenses={setAnnualExpenses}
                    annualSales={annualSales}
                    annualCostOfSales={annualCostOfSales}
                    annualExpenses={annualExpenses}
                    errors={{
                        annualSalesError:props.errors.annualSalesError,
                        annualCostOfSalesError:props.errors.annualCostOfSalesError,
                        annualExpensesError:props.errors.annualExpensesError,
                    }}
                />
                <DebtHistory
                    show={true}
                    hasHistory={hasDebtHistory}
                    setHasHistory={setHasDebtHistory}
                    debtType={debtType}
                    setDebtType={setDebtType}
                    emptyCheck={isEmpty}
                    errors={{
                        debtTypeError:props.errors.debtTypeError
                    }}
                />
            </div>
        );
    }
}

export default Content;