import React, {useEffect, useState} from "react";
import Slider from "@material-ui/core/Slider";
import Input from "@material-ui/core/Input";
import {makeStyles} from "@material-ui/core/styles";
import {BsInfoCircleFill} from "react-icons/bs";

const TeamSize = (props) => {

    const valuetext = (value)=> {
        props.setTeamSize(value);
        if(value === 1){
            return "1 - 5";
        }
        else if(value === 2){
            return "5 - 10";
        }
        else if(value === 3){
            return "10 - 15";
        }
        else{
            return "15 - 20";
        }
    }
    
    const marks = [
    {
        value: 1,
        label: "1 - 5",
    },
    {
        value: 2,
        label: '5 - 10',
    },
    {
        value: 3,
        label: '10  - 15',
    },
    {
        value: 4,
        label: '15 - 20',
    },
    ];

    const displayTeamSizePrompt = ()=>{
        var stage_of_business = Number(localStorage.getItem('stageOfBusiness'));
        return stage_of_business === 10 || stage_of_business === 20? <h5 className="step-heading">Please select the number of potential employees you plan to hire.</h5>:<h5 className="step-heading">Please select the number of new employees that will be hired once you've received funding.</h5>
    }

    return(
        <div className="input-section" style={props.show?{width:"100%",marginTop:20}:{display:"none"}}>
             {/* check the stage of the business and display relevant prompt */}
             {displayTeamSizePrompt()}
            <br/><br/>
            <div style={{width:"80%"}}>
                <Slider
                    min={1}
                    max={4}
                    defaultValue={props.teamSize}
                    valueLabelFormat={valuetext}
                    step={1}
                    marks={marks}
                />
            </div>
            <p className="errorMessage" style={(props.teamSize===0)&&(props.errors)?null:{display:"none"}}>
                {props.errors}<BsInfoCircleFill className="infoBtn"/>
            </p>
            <br/>
        </div>
    );
}

export default TeamSize;