import React, {useState} from "react";
import {BsInfoCircleFill} from "react-icons/bs";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

const DebtHistory = (props) => {
    const styles = {
        removeElement: {
            display: "none",
        },
        marginTopForty:{
            marginTop:40,
        },
        cardStylingBorder:{
            border: "solid 1px #007A4D",
            background:"#007A4D",
            color:"#ffffff",
        },
        cardStylingNoBorder:{
            border:"none",
        },
        debtInputAmount:{
            display:"flex",
            justifyContent:"space-around"
        },
        fullWidth: {
            width:"100%"
        }
    }
    const handleChange = (event) => {
        props.setDebtType(event.target.value);
    };

    const handleDebtOption = (event) => {
        let chosen = event.target.id;
        (chosen==='availableRecords')?props.setHasHistory(true):props.setHasHistory(false);
    }

    return(
        <div className="input-section" style={props.show?null:styles.removeElement}>
            <h3 className="step-heading" style={styles.marginTopForty}>Debt History</h3>
            <div className="application-form inline-input-elements">
                <button className={`card1`} id="noRecords" onClick={handleDebtOption} style={!props.hasHistory?styles.cardStylingBorder:styles.cardStylingNoBorder}>
                    <h6 className="step-heading" id="noRecords">No Debt History</h6>
                    <div className="go-corner" id="noRecords"></div>
                </button>
                <button className={`card1`} id="availableRecords" onClick={handleDebtOption} style={props.hasHistory?styles.cardStylingBorder:styles.cardStylingNoBorder}>
                    <h6 className="step-heading" id="availableRecords">Have Debt History</h6>
                    <div className="go-corner" id="availableRecords"></div>
                </button>
            </div>
            <div className="application-form" style={(props.hasHistory===true)?null:styles.removeElement}>
                <br/>
                <h5 className="step-heading">Please select the type of debt.</h5>
                <br/>
                <div style={styles.fullWidth}>
                    <FormControl component="fieldset">
                        <RadioGroup aria-label="debt" name="debt1" value={props.debtType} onChange={handleChange}>
                            <FormControlLabel value="Business debt" control={<Radio color="default"/>} label="Business debt" />
                            <FormControlLabel value="Personal Debt" control={<Radio color="default"/>} label="Personal debt" />
                            <FormControlLabel value="Both personal and business debt" control={<Radio color="default"/>} label="Both personal and business debt" />
                        </RadioGroup>
                    </FormControl>
                    <p className="errorMessage" style={props.errors.debtTypeError?null:styles.removeElement}>
                        {props.errors.debtTypeError}<BsInfoCircleFill className="infoBtn"/>
                    </p>
                </div><br/>
            </div>
        </div>
    );
}

export default DebtHistory;