import React from 'react';
import {BsPerson} from "react-icons/bs";
import Loader from "react-loader-spinner";
const Consultant = (props) => {
    return(
        <>
            <p id="coursesHeading">Consultants</p>
            <div id="coursesContainer">
                {props.consultantData == null?<Loader type="ThreeDots" color="#212529" height={50} width={50}/>:
                    props.consultantData.map((item,index)=>{
                        return(
                            <div key={index} className={props.selectedConsultantId == item.consultantID?"course selected-course":"course"} onClick={()=>props.selectConsultant(item.consultantID,item)}>
                                <img src={`https://testing-bt.s3.amazonaws.com/${item.image}`} alt="Mutualism Consultant" width={60} height={60}/>
                                <div className="courseInfo">
                                    <h6>{item.first_name} {item.last_name}</h6>
                                    <p>{item.bio.substring(0,50)}...</p>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </>
    );
}

export default Consultant;