import React,{useState,useEffect} from 'react';
import SubmittedIcon from '../Images/Others/submitted.svg';
import Popup from "reactjs-popup";
import Loader from "react-loader-spinner";
import {authentication} from '../firebase/firebase';
import dateFormat from "dateformat";
const ReviewApplication = (props)=>{
    const [applicationDetails,setApplicationDetails] = useState();
    // currency display configurations
    const localization = {style:'currency', currency: 'ZAR'};
    // method to display currency data
    const displayAsCurrency = (value) => new Intl.NumberFormat('en-ZA', localization).format(value);
    useEffect(()=>{
        fetch(`https://mutualism-test.herokuapp.com/api/getApplicationDetails?userId=${authentication.currentUser.uid}`)
        .then(re=>re.json())
        .then(re=>{
            var data = JSON.parse(re.data)[0].fields;
            setApplicationDetails(data);
            // console.log(data);
        })
    },[])


    return(
        <>
        <div className="application-form inline-input-elements">
            <div className="application-submitted" id="soweto"  style={{border: "none"}}>
                <img src={SubmittedIcon} width={100} id="submittedIcon"/>
                <h6 className="step-heading location-heading" id="soweto">Your application has been received.</h6>
                <p className="small step-heading" id="soweto">To review your application, click the review button below.</p>
                <p>Application status: <span id="applicationStatus">{props.applicationStatus}</span> </p>
                <Popup className="applicationReviewPopup" modal trigger={<button className="status-button-prompt action-button">Review application</button>}>
                    {applicationDetails == undefined?
                        <Loader type="ThreeDots" color="#212529" height={50} width={50}/>
                        :
                        <div className="application-review">
                            <h3 className="step-heading location-heading" >Funding application.</h3>
                            <p className="reviewHeading">Application status</p>
                            <p className="reviewValue" id="applicationStatus">{applicationDetails.application_status}</p>
                            <p className="reviewHeading">Application submitted on</p>
                            <p className="reviewValue">{dateFormat(applicationDetails.application_date,"dddd dS mmmm, yyyy")}</p>
                            <p className="reviewHeading">Business CIPC number</p>
                            <p className="reviewValue">{applicationDetails.business_cipc_number}</p>
                            <p className="reviewHeading">Business SARS number</p>
                            <p className="reviewValue">{applicationDetails.business_sars_number}</p>
                            <p className="reviewHeading">Business description</p>
                            <p className="reviewValue">{applicationDetails.business_description}</p>
                            <p className="reviewHeading">Business location</p>
                            <p className="reviewValue">{applicationDetails.business_location}</p>
                            <p className="reviewHeading">Business sector</p>
                            <p className="reviewValue">{applicationDetails.business_sector}</p>
                            <p className="reviewHeading">Business stage</p>
                            <p className="reviewValue">{applicationDetails.business_stage}</p>
                            <p className="reviewHeading">Business' team size</p>
                            <p className="reviewValue">{applicationDetails.business_team_size}</p>
                            <p className="reviewHeading">Required assets</p>
                            <p className="reviewValue">{applicationDetails.required_asset}</p>
                            <p className="reviewHeading">Cost of required asset</p>
                            <p className="reviewValue">{displayAsCurrency(applicationDetails.asset_price)}</p>
                            <p className="reviewHeading">Business has existing debt</p>
                            <p className="reviewValue">{applicationDetails.business_has_existing_debt? "Yes":"No"}</p>
                            <p className="reviewHeading">Debt type</p>
                            <p className="reviewValue">{applicationDetails.business_debt_type}</p>
                            <p className="reviewHeading">Estimated annual sales</p>
                            <p className="reviewValue">{displayAsCurrency(applicationDetails.business_annual_sales)}</p>
                            <p className="reviewHeading">Estimated annual cost of sales</p>
                            <p className="reviewValue">{displayAsCurrency(applicationDetails.business_annual_cost_of_sales)}</p>
                            <p className="reviewHeading">Estimated annual expense</p>
                            <p className="reviewValue">{displayAsCurrency(applicationDetails.business_annual_expenses)}</p>
                        </div>
                    }
                </Popup> 
            </div>
        </div>
        </>
    )
}
export default ReviewApplication