import React , {useEffect,useState} from 'react';
import Loader from "react-loader-spinner";
const Courses = (props)=>{
    const [selectedCourse,setSelectedCourse] = useState(0);
    const selectCourse = (selected_course,course_data)=>{
        setSelectedCourse(selected_course,course_data);
        props.selectCourse(course_data);
    }
    return(
        <>
            <p id="coursesHeading">Courses</p>
            <div id="coursesContainer">
                {props.courses == null?<Loader type="ThreeDots" color="#212529" height={50} width={50}/>:
                    props.courses.map((item,index)=>{
                        return(
                            <div key={index} className={selectedCourse == index?"course selected-course":"course"} onClick={()=>selectCourse(index,item)}>
                                <img src={`https://testing-bt.s3.amazonaws.com/${item.course_image}`} alt="Mutualism Consultant" width={60} height={60}/>
                                <div className="courseInfo">
                                    <h6>{item.course_name}</h6>
                                    <p>{item.course_description.substring(0,50)}...</p>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </>
    )
}
export default Courses