import React, {useState} from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;
const QuizDocument = (props) => {
    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);

    function onDocumentLoadSuccess({ numPages }) {
        setNumPages(numPages);
        setPageNumber(1);
    }

    function changePage(offset) {
        setPageNumber(prevPageNumber => prevPageNumber + offset);
    }

    function previousPage() {
        changePage(-1);
    }

    function nextPage() {
        changePage(1);
    }

    return(
        <>
           <div id="pdfContainer" style={{display:"flex", flexDirection:"column", justifyContent:"space-around", alignContent:"center"}}>
                <Document
                    file={props.document}
                    onLoadSuccess={onDocumentLoadSuccess}
                >
                    <Page pageNumber={pageNumber} style={{marginLeft:"auto", marginRight:"auto"}}/>
                </Document>
                <div style={{marginBottom:"20px"}}>
                    <p style={{textAlign:"center"}}>
                        Page {pageNumber || (numPages ? 1 : '--')} of {numPages || '--'}
                    </p>
                    <div className="courseNavigation">
                        <button
                            type="button"
                            className="quiz-next-page-btn"
                            disabled={pageNumber <= 1}
                            onClick={previousPage}
                        >
                            Previous
                        </button>
                        <button
                            type="button"
                            className="quiz-next-page-btn right-quiz-btn"
                            disabled={pageNumber >= numPages}
                            onClick={nextPage}
                        >
                            Next
                        </button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default QuizDocument;