import React from 'react';
const LocationOptions = (props)=>{

    const styles = {
        buttonSelected: {border: "solid 1px #007A4D", background:"#007A4D", color:"#ffffff"},
        buttonNotSelected: {border: "none"},
    }

    const handleLocationOption = (event) => {
        let chosen = event.target.id;
        (chosen==='johannesburg')?props.selectLocation(chosen):(chosen==='cape_town')?props.selectLocation(chosen):props.selectLocation('');
    }

    return(
        <div className="application-form inline-input-elements">
            <button className={`card1`} id="johannesburg" onClick={handleLocationOption} style={props.chosenLocation==='johannesburg'?{border: "solid 1px #007A4D", background:"#007A4D", color:"#ffffff"}:{border: "none"}}>
                <h6 className="step-heading location-heading" id="johannesburg">Johannesburg</h6>
                <p className="small step-heading" id="johannesburg">Select this option if business is based in Johannesburg.</p>
                <div className="go-corner" id="johannesburg"></div>
            </button>
            <button className={`card1`} id="cape_town" onClick={handleLocationOption} style={props.chosenLocation==='cape_town'?{border: "solid 1px #007A4D", background:"#007A4D", color  :"#ffffff"}:{border: "none"}}>
                <h6 className="step-heading location-heading" id="cape_town">Cape Town</h6>
                <p className="small step-heading" id="cape_town">Select this option if business is based in Cape Town.</p>
                <div className="go-corner" id="cape_town"></div>
            </button>
        </div>
    )
}
export default LocationOptions