import React, {useState,useEffect} from 'react';
import Loader from "react-loader-spinner";
import Popup from "reactjs-popup";
import Confetti from 'react-confetti';
import {BsInfoCircleFill} from "react-icons/bs";
const Quiz = (props) => {

    // States for the Quiz components
    let err = false;
    const [errMessage, setErrMessage] = useState({errQuestionsLeftBlank:''});
    const [submitScore,setSubmitScore] = useState(false);
    const [submitting, setSubmitting] = useState(false);
    const [quizScore,setQuizScore] = useState(0);

    // variables for numbering the quiz questions and accumulating the score for user.
    let number = 0;
    let questionScores = new Array(props.questions.length).fill(null);
    let score;

    // Adding total marks in quiz
    const sumScore = (totalScore, questionScore)=>totalScore+questionScore;

    // Calculating the total score of the quiz as a percentage
    const calculateTotalScore = () => {
        // Adding up all the scores in the questionScores array
        (questionScores.length!==0)?score=questionScores.reduce(sumScore):score=0;
        score=score/(props.questions.length)*100;
        setQuizScore(score);
        submitResults(score);
    }

    // Function to ensure that all the questions are answered before submission
    const validateQuizAnswers = () => {
        if(questionScores.includes(null)){
            err = true;
            setErrMessage({errQuestionsLeftBlank:'Please make sure you have attempted all the questions before submitting.'});
        }else{
            err = false;
            setErrMessage({errQuestionsLeftBlank:''});
        }
    }

    const Question = (props) => {
        const markQuestion = (e) => {
            let selectedChoice = e.target.value;
            props.correctAnswer===selectedChoice?questionScores[props.questionPosition]=1:questionScores[props.questionPosition]=0;
            // console.log(questionScores);
        }
        return (
            <div className="questionBlock" onChange={(event)=> markQuestion(event)}>
                <p className="question">{props.questionNumber}. {props.question}</p>
                {props.options.map((option, index) =>
                    <Options key={index} option={option} number={props.questionNumber} key={index} id={index}/>
                )}
            </div>
        )
    }

    // Child componencnt of the question component to render the different options for each question
    const Options = (props) => {
        return(
            <div className="questionOption">
                <input
                    type="radio"
                    name={`question_${props.number}_options`}
                    value={props.option}
                    id={props.id}
                />
                <label htmlFor={props.id}>{props.option}</label><br/>
            </div>
        );
    }

    const ErrMessage = () => {
        return(
            <div className="quizErrors" style={errMessage.errQuestionsLeftBlank?null:{display:"none"}}>
                <p className="errorMessage">
                    {errMessage.errQuestionsLeftBlank}<BsInfoCircleFill className="infoBtn"/>
                </p>
            </div>
        );
    }

    const handleClick = () => {
        validateQuizAnswers();
        if(!err){
            calculateTotalScore();
            setSubmitScore(!submitScore);
        }
    }

    const CompleteQuiz = () => {
        return(
            <div className="submit-quiz">
               <div onClick={handleClick}> Submit Quiz </div>
            </div>
        );
    }

    const submitResults = (score)=>{
        fetch(`https://mutualism-test.herokuapp.com/api/submitQuizResults`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            body:`userId=${props.user_id}&quizId=${props.quiz_id}&score=${score}`,
        })
        .then(re=>re.json())
        .then(re=>{
            props.displayNewScore();
        });
    }

    const PopupContent = () => {
        return(
            <Popup open={submitScore} onClose={()=>setSubmitScore(!submitScore)} modal>
                {submitting === false?
                    <>
                        {quizScore===100?
                            <>
                                <Confetti className="confetti popup-overlay" id="distinction"/>
                                <h1 className="status-heading">You have Passed!</h1>
                                <p className="status-body" style={{textAlign: 'center', fontWeight: 200, width: '80%'}}>
                                    Congratulations on completing the "{props.quiz_name}" quiz. Your score is <strong
                                    style={{fontWeight: "900", color: "#007A4D"}}>{quizScore}%</strong>. Very well done
                                    getting all the quiz questions correct. You can move on to another quiz.
                                </p>
                            </>:
                            quizScore >= 50 ?
                                <>
                                    <h1 className="status-heading">You have Passed!</h1>
                                    <p className="status-body" style={{textAlign: 'center', fontWeight: 200, width: '80%'}}>
                                        Congratulations on completing the "{props.quiz_name}" quiz. Your score is <strong
                                        style={{fontWeight: "900", color: "#007A4D"}}>{quizScore}%</strong>. Feel free to
                                        move on to another quiz or retake the quiz to improve your score.
                                    </p>
                                </> :
                                <>
                                    <h1 className="status-heading">Don't give up!</h1>
                                    <p className="status-body" style={{textAlign: 'center', fontWeight: 200, width: '80%'}}>
                                        You need at least 50% to pass the  "{props.quiz_name}" quiz. Your current score is <strong
                                        style={{fontWeight: "900", color: "red"}}>{quizScore}%</strong>. Go through the course material and try again.
                                    </p>
                                </>
                        }
                        <button className="form-control sign-in-element submit-button application-form-button" onClick={()=>setSubmitScore(!submitScore)}>
                            Ok
                        </button>
                    </>
                    :
                    <Loader type="Grid" color="#212529" height={100} width={100}/>
                }
            </Popup>
        );
    }

    return(
        <div >
            <div style={{display:"flex", flexDirection:"column", justifyContent:"space-around", alignItems:"center", marginTop:"25px"}}>
                {props.questions.map((question, index)=> {
                    number++;
                        return (
                            <Question key={index} question={question.question} options={question.options} correctAnswer={question.correctOption} questionNumber={number} questionPosition={index}/>
                        )
                    }
                )}
            </div>
            <ErrMessage/>
            <CompleteQuiz/>
            <PopupContent/>
        </div >
    )
}

export default Quiz;