import React, {useState,useEffect} from 'react';
import Conusltant from './ConsultingV2/Consultant';
import ConsultantBio from './ConsultingV2/ConsultantBio';
import Popup from "reactjs-popup";
import Loader from "react-loader-spinner";
import dateFormat from "dateformat";
const Consulting = (props)=>{
    const [consultantData, setConsultantData] = useState();
    const [selectedConsultantId,setSelectedConsultantId] = useState(0);
    const [consultantProfile,setConsultantProfile] = useState();
    const [submittingBooking, setSubmittingBooking] = useState(false);
    const [viewPopUp,setViewPopUp] = useState(false);
    const [selectedSlot,setSelectedSlot] = useState();

    useEffect(()=>{
        // get consultants data
        makeAPICall(getConsultantsUrl, getAPIResponse, populateConsultantData);

    }, [submittingBooking]);

    // api call
    const makeAPICall = (url, getAPIResponseMethod, processRetrievedDataMethod, email, slot) => {
        email = email||false; //make email and slot parameters optional
        slot = slot||false;
        fetch(url(email, slot))
            .then(getAPIResponseMethod)
            .then(processRetrievedDataMethod);
    }

      //Fetch API methods and constants
    const getConsultantsUrl = (email, slot) => {
        email = email||false; //email and slot not needed for this method
        slot = slot|| false;
        return `https://mutualism-test.herokuapp.com/api/getConsultants`;
    }

    const scheduleBookingUrl = (email, slot) => `https://mutualism-test.herokuapp.com/api/scheduleBooking?email=${email}&availability_id=${slot}`;

    const getAPIResponse = response => response.json();

    const populateConsultantData = consultant => {
        let consultantData = consultant.data;
        setConsultantData(consultantData);
        // set default selected consultant
        setSelectedConsultantId(consultant.data[0].consultantID);
        setConsultantProfile(consultant.data[0])
    }

    const submitBookingRequest = () => {
        setSubmittingBooking(false);
        setViewPopUp(false);
    }

    const selectConsultant = (consultant_id,consultant_data)=>{
        setSelectedConsultantId(consultant_id);
        setConsultantProfile(consultant_data);    
    }

    // show booking popup
    const confirmBooking = (TimeSlot)=>{
        setViewPopUp(true);
        setSelectedSlot(TimeSlot);
    }

    const handleBooking=()=>{
        setSubmittingBooking(true);
        makeAPICall(scheduleBookingUrl, getAPIResponse, submitBookingRequest, consultantProfile.email, selectedSlot.pk);
    }

    return (
        <>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-xl-3 .col-12 left-learn-col">
                        <div className="left-side-bar">
                            <Conusltant consultantData={consultantData} selectedConsultantId={selectedConsultantId} selectConsultant={selectConsultant}/>
                        </div>
                    </div>
                    <div className="col-xl-6 col-12">
                       {consultantProfile !== undefined? <ConsultantBio consultant={consultantProfile} confirmBooking={confirmBooking}/>:null}
                    </div>
                    <div className="col-xl-3 col-12 right-col">
                        <div className="course-price">
                            <p>Contact details</p>
                            {consultantProfile !== undefined?
                                <div id="consultantContact">
                                    <a href={`mailto:${consultantProfile.email}`} id="consultantEmail"><span>{consultantProfile.email}</span></a>
                                    <span>{consultantProfile.mobile}</span>
                                </div>
                                :
                                null
                            }
                        </div>
                        <div className="number-of-quizzes number-of-languages">
                            <p id="quizCountHeader">Languages spoken</p>
                            <div id="languages">
                                {consultantProfile !== undefined?
                                    consultantProfile.languages.map((language,index)=>
                                        <div className="languageCard" key={index}><p className="languageStyle">{language}</p></div>   
                                    )
                                    :
                                    null
                                }
                            </div>
                            
                        </div>
                        <div className="completed-quizzes">
                            <p>Recent consultations</p>
                            <br/>
                            
                        </div>  
                    </div>
                </div>
            </div>
            <Popup open={viewPopUp} onClose={()=>setViewPopUp(false)} closeOnDocumentClick={submittingBooking == false?true:false}>
                {submittingBooking === false && consultantProfile !== undefined && selectedSlot !== undefined?
                    <>
                        <h1 className="status-heading">Confirm booking.</h1>
                        <p className="status-body">
                            You are booking a consultation with {consultantProfile.first_name} {consultantProfile.last_name} for {dateFormat(selectedSlot.fields.date, 'dddd dS mmmm')} at {selectedSlot.fields.time}.
                        </p>
                        <button
                            className="form-control sign-in-element submit-button application-form-button"
                            onClick={()=>handleBooking()}
                        >
                            Confirm
                        </button>
                    </>
                    :
                    <Loader type="Grid" color="#212529" height={100} width={100}/>
                }
            </Popup>
        </>
    )
}
export default Consulting