import React, {useState,useEffect} from 'react';
import Popup from "reactjs-popup";
import Loader from "react-loader-spinner";
import ApplicationSteps from './ApplicationSteps';
import Step1 from '../Components/ApplicationSteps/Step1';
import Step2 from '../Components/ApplicationSteps/Step2';
import Step3 from '../Components/ApplicationSteps/Step3';
import Step4 from '../Components/ApplicationSteps/Step4';
import {authentication} from '../firebase/firebase';
import ReviewApplication from './ReviewApplication';
const Applicant = ()=>{
    // States for the component
    const [activeStep,setActiveStep] = useState(1);
    // const [showButtons,setShowButtons] = useState(false);
    const [confirm,setConfirm] = useState(false);
    const [submitting,setSubmitting] = useState(false);
    const [defaultStep1,setDefaultStep1] = useState(0);
    const [hasExistingApplication,setHasExistingApplication] = useState();
    const [applicationStatus,setApplicationStatus] = useState();
    const [selectedFinancialFileUpload, setSelectedFinancialFileUpload] = useState(null);
    const [businessDetailsStage, setBusinessDetailsStage] = useState(()=>{
        let completedStage = JSON.parse(localStorage.getItem('applicationStage'));
        return completedStage>=2?2:1;
    });
    const [completedStage, setCompletedStage] = useState(()=>{
        let completedStage = localStorage.getItem('applicationStage');
        // console.log(completedStage);
        return completedStage?JSON.parse(completedStage):0;
    });

    // Component Styles
    const styles = {
        removeElement: {
            display:"none",
        },
        popupText:{
            textAlign:'center',
            fontWeight:200,
            width:'80%',
        }
    }

    const navigateNext = () => {
        setCompletedStage(activeStep);
        localStorage.setItem('applicationStage', JSON.stringify(activeStep));
        setActiveStep(activeStep+1);
    }

    const nextStep =()=>{
        activeStep < 4? navigateNext():setActiveStep(activeStep);
        // check if user is submitting
        if(activeStep === 4){
            // console.log("submit ",activeStep);
            setConfirm(true);
        }
        // console.log(activeStep);
    }
    

    const previousStep = (step)=>{
        // take the user back to last step of step 1.
        if(step === "step 2"){
            setDefaultStep1(2);
        }
        activeStep > 1? setActiveStep(activeStep-1):setActiveStep(activeStep);
    }

    const renderStepContent = ()=>{
        if(activeStep === 1 ){
            return <Step1
                nextStep={nextStep}
                step={defaultStep1}
                completedStage={completedStage}
                setCompletedStage={setCompletedStage}
                activeStep={activeStep}
                setActiveStep={setActiveStep}
            />
        }
        else if(activeStep === 2){
            return <Step2
                nextStep={nextStep}
                previousStep={()=>previousStep("step 2")}
                businessDetailsStage={businessDetailsStage}
                setBusinessDetailsStage={setBusinessDetailsStage}
                completedStage={completedStage}
                setCompletedStage={setCompletedStage}
            />
        }else if(activeStep ===3){
            return <Step3
                nextStep={nextStep}
                previousStep={()=>previousStep("step 2")}
                businessDetailsStage={businessDetailsStage}
                setBusinessDetailsStage={setBusinessDetailsStage}
                completedStage={completedStage}
                setCompletedStage={setCompletedStage}
            />
        }else{
            return <Step4
                selectedFinancialFileUpload={selectedFinancialFileUpload}
                setSelectedFinancialFileUpload={setSelectedFinancialFileUpload}
                nextStep={nextStep}
                previousStep={previousStep}
                completedStage={completedStage}
                setCompletedStage={setCompletedStage}
            />
        }
    }

    const submitApplication = ()=>{
        setSubmitting(true);
        // get items
        let user_id = authentication.currentUser.uid;
        let business_stage = localStorage.getItem("stageOfBusiness");
        let business_location = localStorage.getItem("userLocation");
        let business_sector = JSON.parse(localStorage.getItem("industryType")).value;
        let business_team_size = Number(localStorage.getItem("teamSize"));
        let business_description = localStorage.getItem("businessDescription");
        let business_cipc_number = localStorage.getItem("businessRegistrationProofCipc");
        let business_sars_number = localStorage.getItem("businessRegistrationProofSars");
        let annual_sales = localStorage.getItem('annualSales');
        let annual_cost_of_sales = localStorage.getItem('annualCostOfSales');
        let annual_expenses = localStorage.getItem('annualExpenses');
        let business_has_existing_debt = JSON.parse(localStorage.getItem("hasDebtHistory"));
        let business_debt_type = localStorage.getItem("debtType");
        let required_asset = JSON.parse(localStorage.getItem("assetRequiredType")).value;
        let asset_price = localStorage.getItem("assetRequiredPrice");
        let file = selectedFinancialFileUpload;

        // business stage
        if(business_stage === 10){
            business_stage =  "Ideation";
        }
        else if(business_stage === 20){
            business_stage =  "Newly operational";
        }
        else if(business_stage === 30){
            business_stage =  "Operating";
        }
        else{
            business_stage =  "Growing";
        }

        // team size
        if(business_team_size == 1){
            business_team_size = "1 - 5";
        }else if(business_team_size == 2){
            business_team_size = "5 - 10";
        }else if(business_team_size == 1){
            business_team_size = "10 - 15";
        }else{
            business_team_size = "15 - 20";
        }

        // create form data
        var form_data = new FormData();
        form_data.append("userId", user_id);
        form_data.append("business_stage", business_stage);
        form_data.append("business_location", business_location);
        form_data.append("business_sector", business_sector);
        form_data.append("business_team_size", business_team_size);
        form_data.append("business_description", business_description);
        form_data.append("business_cipc_number", business_cipc_number);
        form_data.append("business_sars_number", business_sars_number);
        form_data.append("annual_sales", annual_sales);
        form_data.append("annual_cost_of_sales", annual_cost_of_sales);
        form_data.append("annual_expenses", annual_expenses);
        form_data.append("file", file);
        form_data.append("business_has_existing_debt", business_has_existing_debt);
        form_data.append('business_debt_type', business_debt_type);
        form_data.append("required_asset", required_asset);
        form_data.append("asset_price", asset_price);
        
        fetch(`https://mutualism-test.herokuapp.com/api/createApplication`,{
            method: 'POST',
            body:form_data
        })
        .then(re=>re.json())
        .then(re=>{
            setSubmitting(false);
            setConfirm(false);
            setHasExistingApplication(true);
        })
    }

    const getApplicationStatus = ()=>{
        fetch(`https://mutualism-test.herokuapp.com/api/getApplicationStatus?userId=${authentication.currentUser.uid}`)
        .then(re=>re.json())
        .then(re=>{
            setHasExistingApplication(re.data);
            setApplicationStatus(re.application_status);
        })
    }

    useEffect(()=>{
        getApplicationStatus();
    },[hasExistingApplication])



    return(
        <>
            {hasExistingApplication === false?<ApplicationSteps activeStep={activeStep}/>:null}
            {hasExistingApplication === undefined?
                <div className="application-form inline-input-elements">
                    <Loader type="ThreeDots" color="#212529" height={50} width={50}/>
                </div>
                :
                hasExistingApplication === false?
                    renderStepContent()
                :
                <ReviewApplication applicationStatus={applicationStatus}/>
            }



            <Popup open={confirm} onClose={()=>setConfirm(false)}>
                {submitting === false?
                    <>
                        <h1 className="status-heading">Application complete.</h1>
                        <p className="status-body" style={styles.popupText}>
                            Application completed successfully, please confirm submission, by clicking the submit button below.
                        </p>
                        <button onClick={submitApplication} className="form-control sign-in-element submit-button application-form-button">
                            Submit
                        </button>
                    </>
                    :
                    <Loader type="Grid" color="#212529" height={100} width={100}/>
                }
            </Popup>
        </>
    )
}
export default Applicant