import React, {useState} from "react";
import _ from 'lodash';
import {BsInfoCircleFill} from "react-icons/bs";
import dateFormat from "dateformat";
import NumberFormat from "react-number-format";
import PrivacyPolicy from '../../../Resources/PrivacyPolicy.pdf';
import TermsAndConditions from '../../../Resources/Terms&Conditions.pdf';
import CookiePolicy from '../../../Resources/CookiePolicy.pdf';
import {AiFillFile} from 'react-icons/ai';

const Content = (props) => {

    const localization = {style:'currency', currency: 'ZAR'};
    const displayAsCurrency = (value) => new Intl.NumberFormat('en-ZA', localization).format(value);

    const styles = {
        emphasis: {
            fontWeight: 600,
            color: '#007a4d',
        },
        importantNotice:{
            fontWeight: 500,
            listStyleType: "none",
        },
        noScrollInReviewPane:{
            overflowY: "hidden",
        },
        fileUpload:{
            width:"100%",
            minHeight:"50px",
            height:"auto",
        },
        fileNameStyle:{
            color:"#007A4D",
            margin: 0,
        },
        fileDescriptionStyle:{
            margin:0,
            fontSize:"10px",
        },
        errorFileUpload:{
            width:"100%",
            minHeight:"50px",
            height:"auto",
            borderColor:"#cd0303",
            background:"#fa9898",
            color:"#cd0303",
        },
        hideElement:{
            display:"none",
        },
        legalLinks:{
            color:"#007A4D",
        },
        legalCheckPosition:{
            height: "15px",
            width: "15px",
        },
    };
    const financialsSelectFile = (event) => {
        if (event.target.files[0]) {
            props.setSelectedFinancialFileUpload(event.target.files[0]);
            props.setSelected(true);
        }
    };
    return (
        <div className="input-section">
            <div className="applicationInfo application-form">
                <div className="application-review" style={styles.noScrollInReviewPane}>
                    <h3 className="step-heading location-heading" >Asset Financing Application.</h3>
                    <p className="reviewHeading">Business Location</p>
                    <p className="reviewValue">{props.location.split('_').map((word)=>_.capitalize(word)).join(' ')}</p>
                    <p className="reviewHeading">Business Stage</p>
                    <p className="reviewValue">{props.stage}</p>
                    <p className="reviewHeading">Industry Type</p>
                    <p className="reviewValue">{props.industryType.label}</p>
                    <p className="reviewHeading">Team Size</p>
                    <p className="reviewValue">{props.teamSize}</p>
                    <p className="reviewHeading">Business description</p>
                    <p className="reviewValue">{props.description}</p>
                    <p className="reviewHeading">Business CIPC number</p>
                    <p className="reviewValue"><NumberFormat
                        value={props.CIPC}
                        displayType={'text'}
                        format="####/######/##"
                    /></p>
                    <p className="reviewHeading">Business SARS number</p>
                    <p className="reviewValue">{props.SARS}</p>
                    <p className="reviewHeading">Expected annual sale</p>
                    <p className="reviewValue">{displayAsCurrency(props.annualSales)}</p>
                    <p className="reviewHeading">Expected annual cost of sale</p>
                    <p className="reviewValue">{displayAsCurrency(props.annualCostOfSales)}</p>
                    <p className="reviewHeading">Expected annual expenses</p>
                    <p className="reviewValue">{displayAsCurrency(props.annualExpenses)}</p>
                    <div>
                        <p className="reviewValue">To complete this application, proof of financial records are required. These can be provided in the form of:</p>
                        <ul className="reviewValue" style={styles.importantNotice}>
                            <li>Company bank statement for at least the past <span style={styles.emphasis}>3 months</span>.</li>
                            <li>Any available financial statements or reports.</li>
                            <li>The file upload must be a pdf document.</li>
                        </ul>
                        <div>
                            <div className="selectFileBtn" style={(!props.selected&&props.errors.neededUploadsNotSelected)?styles.errorFileUpload:styles.fileUpload}>
                                <input type="file" id="fileInput" onChange={financialsSelectFile} accept=".pdf"/>
                                    {!props.selected?
                                        "Select file or drop file"
                                        :
                                        <div style={{display:"flex", flexDirection:"row"}}>
                                            <div><AiFillFile size='3em' color={'#007A4D'}/></div>
                                            <div>
                                                <h6 className="step-heading" style={styles.fileNameStyle}>{props.selectedFinancialFileUpload.name}</h6>
                                                <p style={styles.fileDescriptionStyle}>{props.selectedFinancialFileUpload.type}</p>
                                                <p style={styles.fileDescriptionStyle}>{(Number(props.selectedFinancialFileUpload.size)/(1024)).toFixed(1)}KB</p>
                                            </div>
                                        </div>
                                    }
                            </div>
                        </div>
                        <p className="errorMessage" style={(!props.selected&&props.errors.neededUploadsNotSelected)?null:styles.hideElement}>
                            {props.errors.neededUploadsNotSelected}<BsInfoCircleFill className="infoBtn"/>
                        </p>
                        <p className="errorMessage" style={(props.selected&&((Number(props.selectedFinancialFileUpload.size)/(1024*1024*2))>1)&&props.errors.fileUploadTooBig)?null:styles.hideElement}>
                            {props.errors.fileUploadTooBig}<BsInfoCircleFill className="infoBtn"/>
                        </p>
                    </div>
                    <p className="reviewHeading">Business Debt History</p>
                    <p className="reviewValue">{props.debtHistory?"Has Debt History":"No Debt History"}</p>
                    <div style={props.debtHistory?null:styles.hideElement}>
                        <p className="reviewHeading">Debt type</p>
                        <p className="reviewValue">{props.debtType}</p>
                    </div>
                    <p className="reviewHeading">Asset Type</p>
                    <p className="reviewValue">{props.assetType.label}</p>
                    <p className="reviewHeading">Cost of required asset</p>
                    <p className="reviewValue">{displayAsCurrency(props.assetCost)}</p>
                    <p className="reviewValue">I have read and agree to the Mutualism Services' <a
                        style={styles.legalLinks}
                        href={TermsAndConditions}
                        target="_blank"
                        rel="noreferrer noopener"
                    >
                        Terms and Conditions. </a><input
                        style={styles.legalCheckPosition}
                        type="checkbox"
                        onChange={()=>props.setAgreedTerms(!props.agreedTerms)}
                    /></p>
                    <p className="errorMessage" style={(!props.agreedTerms&&props.errors.termsConditionsNotAccepted)?null:styles.hideElement}>
                        {props.errors.termsConditionsNotAccepted}<BsInfoCircleFill className="infoBtn"/>
                    </p>
                    <p className="reviewValue">I have read and agree to the Mutualism Services' <a
                        href={CookiePolicy}
                        style={styles.legalLinks}
                        target="_blank"
                        rel="noreferrer noopener"
                    >Cookie Policy. </a><input
                        style={styles.legalCheckPosition}
                        type="checkbox"
                        onChange={()=>props.setAgreedCookies(!props.agreedCookies)}
                    /></p>
                    <p className="errorMessage" style={(!props.agreedCookies&&props.errors.cookiePolicyNotAccepted)?null:styles.hideElement}>
                        {props.errors.cookiePolicyNotAccepted}<BsInfoCircleFill className="infoBtn"/>
                    </p>
                    <p className="reviewValue">I have read and agree to the Mutualism Services' <a
                        href={PrivacyPolicy}
                        style={styles.legalLinks}
                        target="_blank"
                        rel="noreferrer noopener"
                    >Privacy Policy </a><input
                        style={styles.legalCheckPosition}
                        type="checkbox"
                        onChange={()=>props.setAgreedPrivacy(!props.agreedPrivacy)}
                    /></p>
                    <p className="errorMessage" style={(!props.agreedPrivacy&&props.errors.privacyPolicyNotAccepted)?null:styles.hideElement}>
                        {props.errors.privacyPolicyNotAccepted}<BsInfoCircleFill className="infoBtn"/>
                    </p>
                </div>
            </div>
        </div>
    );
}

export default Content;