import React, {useEffect} from 'react';
import dateFormat from "dateformat";
const ConsultantBio = (props)=>{
    return(
        <> 
            <div id="courseOverview">
                <img src={`https://testing-bt.s3.amazonaws.com/${props.consultant.image}`} alt="Mutualism Consultant" width={100} height={100}/>
                <div id="courseDescription">
                    <h5>{props.consultant.first_name} {props.consultant.last_name}</h5>
                    <p>{props.consultant.bio}</p>
                </div>
            </div>
            <p>Availability</p>
            {
                props.consultant.availability.map((item,index)=>{
                    return(
                        <div key={index} className="quiz">
                            <p>{dateFormat(item.fields.date, "dddd, dS mmmm")} {item.fields.time}</p>
                            <button onClick={()=>props.confirmBooking(item)} style={{width:100}} className="view-qui-btn">Book slot</button>
                        </div>
                    )
                })
            }
        </>
    )
}
export default ConsultantBio