import React, { useState,useEffect } from 'react';
import QuizOverview from './QuizOverview';
import { IoChevronBack,IoWarning } from "react-icons/io5";
import Popup from "reactjs-popup";
const CourseContent = (props)=>{
    const [openQuiz, setOpenQuiz] = useState(false);
    const [selectedQuiz,setSelectedQuiz] = useState();
    const [buyPrompt,setBuyPrompt] = useState(false);
    let localization = {style:'currency', currency: 'ZAR'};
    // method to display currency data
    let displayAsCurrency = (value) => new Intl.NumberFormat('en-ZA', localization).format(value);

    // allow user to view course only if purchased
    const viewQuiz = (quiz)=>{
        var selected_course_id = props.selectedCourse.course_id;
        var purchased_courses = props.purchasedCourseIds;
        var is_this_course_purchased = purchased_courses.includes(selected_course_id);
        if(is_this_course_purchased){
            setOpenQuiz(true);
            setSelectedQuiz(quiz);
        }else{
            // show buy prompt
            setBuyPrompt(true);
        }
    }

    return(
        <>
            <div id="courseOverview">
                <img src={`https://testing-bt.s3.amazonaws.com/${props.selectedCourse.course_image}`} alt="Mutualism Consultant" width={100} height={100}/>
                <div id="courseDescription">
                    <h5>{props.selectedCourse.course_name}</h5>
                    <p>{props.selectedCourse.course_description}</p>
                </div>
            </div>

            <div id="quizes">
                <p>Quizes</p>
                {
                    props.selectedCourse.quizzess.map((item,index)=>{
                        return(
                            <div key={index} className="quiz">
                                <p>{item.fields.quiz_name}</p>
                                <button onClick={()=>viewQuiz(item)} style={{width:100}} className="view-qui-btn">View quiz</button>
                            </div>
                        )
                    })
                }
            </div>

            {/* display quiz when user clicks on view quiz button */}
            {openQuiz == true?
                <div className="takeQuiz animate__animated animate__fadeInUp">
                    <div id="closeQuiz" onClick={()=>setOpenQuiz(false)}>
                        <IoChevronBack size={15}/>
                        <span>Back</span>
                    </div>
                    <QuizOverview quiz={selectedQuiz}/>
                </div>
                :
                null
            }

            {/* show buy prompt if user decides to view a course they haven't purchased yet */}
            <Popup open={buyPrompt} onClose={()=>setBuyPrompt(false)}>
                <IoWarning size={50} color={'#f44336'}/>
                <h5>Uh Oh!</h5>
                <p id="buy-prompt">
                    To view course content please continue by purchasing this course<br/>
                    for only <b>{displayAsCurrency(props.selectedCourse.price)}</b>.
                </p>
            </Popup>

        </>
    )
}
export default CourseContent