import React from "react";
import {BsInfoCircleFill} from "react-icons/bs";
import NumberFormat from "react-number-format";

const BusinessRegistrationProof = (props) => {
    /********************************************* CIPC Registration Number format **************************************/
    // Number format: YYYY/NNNNNN/TT
    // YYYY is the year i.e. 1998 --> constraint:
    //         if company in ideation, newly operational or operating phase: Year = CurrentYear
    //                 if Year < currentYear ==> error: company state doesn't match registration number
    //         if company in growing phase: Year < CurrentYear
    //                 if Year == currentYear ==> error: company state doesn't match registration number
    //         if YYYY > currentYear ==> error: invalid number
    // NNNNNN is a sequebce of numbers to identify the company
    // TT the last two digits identify the type of company which is as follows:
    //         06 PUBLIC COMPANY
    //         07 PRIVATE COMPANY
    //         08 ARTICLE 21
    //         09 LIMITED BY GUARANTEE
    //         10 EXTERNAL COMPANY
    //         12 EXTERNAL COMPANY UNDER SECTION 21A
    //         21 INC
    //         22 UNLIMITED
    //         23 CLOSE CORPORATION
    //         24 PRIMARY COOPERATIVE
    //         25 SECONDARY COOPERATIVE
    //         26 TERTIARY COOPERATIVE
    //         30 STATE OWNED COMPANY (SOC LTD)
    //         31 STATUTORY BODY
    // Additional constraint: 12 digits
    const handleCIPCInput = (formattedValue)=> {
        const currentYear = new Date().getFullYear();
            console.log(currentYear);
        // if(formattedValue) {
        //     const CIPCYear = formattedValue.split('/')[0].toInt();
        //     console.log(currentYear);
        //     console.log(CIPCYear);
        // }
    }
    /********************************************* SARS Registration Number format **************************************/
    // Number format: NNNNNNNNNN
    // Tax number is 10 digits long
    // The income tax number can only start with the digits (0,1,2,3, or 9)
    //         if company in growing phase: Year < CurrentYear
    // The last digit of a tax number is a check digit which can be calculated by the following algorithim:
    //         Digit 9*2 + Digit 8 + Digit 7*2 + Digit 6 + Digit 5*2 + Digit 4 + Digit 3*2 + Digit 2 + Digit 1*2
    //         if the last digit of total is greater than 0, then digit 9 should be the result of 10-(total MOD 10)
    //                 i.e. 10-(total-10*floor(total/10));
    //         If the last digit of total is 0, then digit 9 must also equal 0

        // Component Styles
    const styles = {
            removeElement: {
                display:"none",
            },
            topMarginForty:{
                marginTop:40
            },
            popupText:{
                textAlign:'center',
                fontWeight:200,
                width:'80%',
            }
        }

    return(
        <div className='input-section'>
            <h3 className="step-heading">Registration</h3>
            <div className="application-form inline-input-elements" style={styles.topMarginForty}>
                <div><h5 className="step-heading">Please enter your (CIPC) company registration number </h5></div>
            </div>
            <div style={{width:'80%'}}>
                <NumberFormat
                    className="team-size-input registration-number-input"
                    isNumericString={true}
                    name="cipc-reg"
                    id="cipc-reg"
                    format="####/######/##"
                    placeholder="CIPC registration number..."
                    mask={['y','Y','Y','Y','N','N','N','N','N','N','N','N']}
                    onValueChange={(values)=>{
                        const {formattedValue, value} = values;
                        // handleCIPCInput(formattedValue);
                        props.setCipc(value);
                    }}
                    value={props.cipc}
                    required
                />
                <p className="errorMessage" style={(!props.cipc)&&(props.errors.cipcInputNoText)?null:styles.removeElement}>
                    {props.errors.cipcInputNoText}<BsInfoCircleFill className="infoBtn"/>
                </p>
                <p className="errorMessage" style={(props.cipc.length!==12)&&(props.errors.cipcInputInvalid)?null:styles.removeElement}>
                    {props.errors.cipcInputInvalid}<BsInfoCircleFill className="infoBtn"/>
                </p>
            </div>

            <div className="application-form inline-input-elements" style={styles.topMarginForty}>
                <div><h5 className="step-heading">Please enter your (SARS) company tax registration number</h5></div>
            </div>
            <div style={{width:'80%'}}>
                <NumberFormat
                    className="team-size-input registration-number-input"
                    isNumericString={true}
                    name="sars-reg"
                    id="sars-reg"
                    format="##########"
                    placeholder="SARS registration number..."
                    mask={'_'}
                    onValueChange={(values)=>{
                        const {formattedValue, value} = values;
                        props.setSars(value);
                    }}
                    value={props.sars}
                    required
                />
                <p className="errorMessage" style={!props.sars&&props.errors.sarsInputNoText?null:styles.removeElement}>
                    {props.errors.sarsInputNoText}<BsInfoCircleFill className="infoBtn"/>
                </p>
                <p className="errorMessage" style={(props.sars.length!==10)&&props.errors.sarsInputInvalid?null:styles.removeElement}>
                    {props.errors.sarsInputInvalid}<BsInfoCircleFill className="infoBtn"/>
                </p>
            </div>

            <br/>
        </div>
    );
}

export default BusinessRegistrationProof;