import React, {useState} from 'react';
import Content from "./Step4/Content";
import Navigate from "./Step4/Navigate";

const Step4 = (props) => {
    const [errors, setErrors] = useState({});
    // Application Data
    const [userSelectedLocation] = useState(localStorage.getItem('userLocation'));
    const [businessStage] = useState(()=>{
        const stageOfBusiness = localStorage.getItem('stageOfBusiness');
        switch (Number(stageOfBusiness)){
            case 10:
                return "Ideation";
            case 20:
                return "Newly operational";
            case 30:
                return "Operating";
            case 40:
                return "Growing";
            default:
                break;
        }
    });
    const [industryTypeState] = useState(JSON.parse(localStorage.getItem('industryType')));
    const [teamSize] = useState(()=>{
        var sizeOfTeam = Number(localStorage.getItem('teamSize'))
        switch (sizeOfTeam){
            case 1:
                return "1 - 5";
            case 2:
                return "5 - 10";
            case 3:
                return "10 - 15";
            case 4:
                return "15 - 20";
            default:
                break;
        }
    });
    const [summary] = useState(localStorage.getItem('businessDescription'));
    const [cipcReg] = useState(localStorage.getItem('businessRegistrationProofCipc'));
    const [sarsReg] = useState(localStorage.getItem('businessRegistrationProofSars'));
    const [annualSales] = useState(localStorage.getItem('annualSales'));
    const [annualCostOfSales] = useState(localStorage.getItem('annualCostOfSales'));
    const [annualExpenses] = useState(localStorage.getItem('annualExpenses'));

    const [selected, setSelected] = useState(false);
    const [hasDebtHistory] = useState(JSON.parse(localStorage.getItem('hasDebtHistory')));
    const [debtType] = useState(localStorage.getItem('debtType'));
    const [assetType] = useState(JSON.parse(localStorage.getItem('assetRequiredType')));
    const [assetCost] = useState( Number(localStorage.getItem('assetRequiredPrice')));
    const [agreedTerms, setAgreedTerms] = useState(false);
    const [agreedPrivacy, setAgreedPrivacy] = useState(false);
    const [agreedCookies, setAgreedCookies] = useState(false);
    return(
        <div className="step1">
            <h1 className="status-heading">Application Review</h1>
            <p className="status-body">
                Please confirm the details below and upload the required documentation before submitting.
            </p>
            <Content
                location={userSelectedLocation}
                stage={businessStage}
                industryType={industryTypeState}
                teamSize={teamSize}
                description={summary}
                CIPC={cipcReg}
                SARS={sarsReg}
                annualSales={annualSales}
                annualCostOfSales={annualCostOfSales}
                annualExpenses={annualExpenses}
                selectedFinancialFileUpload={props.selectedFinancialFileUpload}
                setSelectedFinancialFileUpload={props.setSelectedFinancialFileUpload}
                selected={selected}
                setSelected={setSelected}
                debtHistory={hasDebtHistory}
                debtType={debtType}
                assetType={assetType}
                assetCost={assetCost}
                agreedTerms={agreedTerms}
                setAgreedTerms={setAgreedTerms}
                agreedPrivacy={agreedPrivacy}
                setAgreedPrivacy={setAgreedPrivacy}
                agreedCookies={agreedCookies}
                setAgreedCookies={setAgreedCookies}
                errors={errors}
            />
            <Navigate
                errors={errors}
                setErrors={setErrors}
                selected={selected}
                selectedFinancialFileUpload={props.selectedFinancialFileUpload}
                agreedTerms={agreedTerms}
                agreedPrivacy={agreedPrivacy}
                agreedCookies={agreedCookies}
                // financials={hasFinancials}
                previousStep={props.previousStep}
                nextStep={props.nextStep}
            />
        </div>
    )
}
export default Step4;