import React from 'react';
const PurchasedCoursesComponent = (props)=>{
    return (
        <div id="coursesContainer">
            {
                props.purchasedCourses.map((item,index)=>{
                    return(
                        <div key={index} className={"selected-course course"} style={{cursor:'unset'}}>
                            <img src={`https://testing-bt.s3.amazonaws.com/${item.course_image}`} alt="Mutualism Consultant" width={60} height={60}/>
                            <div className="courseInfo">
                                <h6>{item.course_name}</h6>
                                {/* <p>{item.course_description.substring(0,50)}...</p> */}
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}
export default PurchasedCoursesComponent