import React, {useState,useEffect} from 'react';
import QuizDocument from './QuizDocument';
import Quiz from './Quiz';
import {authentication} from '../../firebase/firebase';
import {GiAchievement} from "react-icons/gi";
import Loader from "react-loader-spinner";

const QuizOverview = (props) => {
    const [displayDoc,setDisplayDoc] = useState(true);
    const [quizQuestions,setQuizQuestions] = useState(JSON.parse(props.quiz.fields.quiz_quiz)); 
    const [pastScore,setpastScore] = useState();
    
    useEffect(()=>{
        getPastScore();
    },[])

    const getPastScore = ()=>{
        setpastScore(undefined);
        fetch(`https://mutualism-test.herokuapp.com/api/getLastQuizScore?userId=${authentication.currentUser.uid}&quizId=${props.quiz.pk}`)
        .then(re=>re.json())
        .then(re=>{
            setpastScore(Number(re.response));
        })
    }
    return(
        <>
            <div className="quizContent">
                {displayDoc == true?
                    <>
                        <QuizDocument document={`https://testing-bt.s3.amazonaws.com/${props.quiz.fields.quiz_document}`}/>
                    </>
                    :
                    <Quiz
                        questions={quizQuestions} 
                        quiz_name={props.quiz.fields.quiz_name}
                        quiz_id={props.quiz.pk}
                        user_id={authentication.currentUser.uid}
                        displayNewScore={getPastScore}
                    />
                }
                {/* display preview score if user had already attempted the quiz */}
                <div id="pastQuizScore">
                    {pastScore == undefined?<Loader type="ThreeDots" color="#212529" height={20} width={20}/>:
                        // if there is no past score
                        isNaN(pastScore)?
                            null
                        :
                        // if past score is less than 50, fail
                        pastScore < 50?
                            <p className="previousScoreFailed">Previous score: {pastScore}%</p>
                        :
                        <div className="previousScorePassed">
                            <GiAchievement size={40}/>
                            <span>Previous score: {pastScore}%</span>
                        </div>
                        
                    }
                </div>
                <div id="takeQuizBtn" onClick={()=>setDisplayDoc(!displayDoc)}>
                    <span>{displayDoc == true? "Start quiz":"View quiz document"}</span>
                </div>
            </div>
        </>
    )
}

export default QuizOverview;