import {BsChevronLeft, BsChevronRight} from "react-icons/bs";
import React, {useEffect} from "react";

const Navigate = (props) => {
    const prevQuestions = () => {
        (props.businessDetailsStage!==1)?props.setBusinessDetailsStage(props.businessDetailsStage-1):props.previousStep();
    }

    const nextQuestions = () => {
        (props.businessDetailsStage!==2)?props.setBusinessDetailsStage(props.businessDetailsStage+1):props.nextStep();
    }

    let allowNextStage = false;

    const validateInput = () =>{
        const industryTypeState = JSON.parse(localStorage.getItem('industryType')).value;
        const summary = localStorage.getItem('businessDescription').trim();
        const cipcReg = localStorage.getItem('businessRegistrationProofCipc').trim();
        const sarsReg = localStorage.getItem('businessRegistrationProofSars').trim();
        const hasDebtHistory = JSON.parse(localStorage.getItem('hasDebtHistory'));
        const debtType = localStorage.getItem('debtType');
        const getStageOfBusiness = Number(localStorage.getItem('stageOfBusiness'));
        const annualSales = Number(localStorage.getItem('annualSales'));
        const annualCostOfSales = Number(localStorage.getItem('annualCostOfSales'));
        const annualExpenses = Number(localStorage.getItem('annualExpenses'));

        switch(props.businessDetailsStage){
            case 1:
                if((industryTypeState!=="industry")&&(summary.length>=75)) {
                    props.setAllowNextStage(true);
                    allowNextStage=true;
                }else{
                    props.setAllowNextStage(false);
                    allowNextStage=false;
                }
                break;
            case 2:
                let issues = [];
                // if business is operating and growing make sure they submit SARS & CIPC numbers
                // also make sure that business financials have been filled in
                if(getStageOfBusiness === 30 || getStageOfBusiness === 40){
                    if (hasDebtHistory) {
                        issues = [(cipcReg === ''), (sarsReg === ''), (cipcReg.length !== 12), (sarsReg.length !== 10), annualSales === 0, annualCostOfSales === 0, annualExpenses === 0,debtType === null];
                    }else{
                        issues = [(cipcReg === ''), (sarsReg === ''), (cipcReg.length !== 12), (sarsReg.length !== 10),annualSales === 0, annualCostOfSales === 0, annualExpenses === 0,debtType === null];
                    }
                }else{
                    if (hasDebtHistory) {
                        issues = [annualSales === 0, annualCostOfSales === 0, annualExpenses === 0,debtType === null];
                    }else{
                        issues = [annualSales === 0, annualCostOfSales === 0, annualExpenses === 0,debtType === null];
                    }
                }

                issues.includes(true)?allowNextStage=false:allowNextStage=true;
        }
    };

    useEffect(()=>{

    },[props.allowNextStage]);

    const showErrors = () => {
        const errorList = {};
        switch(props.businessDetailsStage){
            case 1:
                if(JSON.parse(localStorage.getItem('industryType')).value==="industry") {
                    errorList.industryInputNoSelect = 'Please select the closest industry sector your business can fall under.';
                }
                if(Number(localStorage.getItem('teamSize'))===0){
                    errorList.teamSizeNoInput = 'Please enter the size of your team.';
                }
                if(localStorage.getItem('businessDescription').trim().length<75){
                    errorList.businessDescriptionTextTooShort = 'Your description must be at least 75 characters long with no trailing spaces.';
                }
                if(localStorage.getItem('businessDescription').trim()==='') {
                    errorList.businessDescriptionNoText = 'Please enter a brief description of your business.';
                }
                break;
            case 2:
                if(localStorage.getItem('businessRegistrationProofCipc').trim()==='' ) {
                    errorList.cipcInputNoText = 'Please enter your business CIPC registration number.';
                }
                if(localStorage.getItem('businessRegistrationProofCipc').trim().length!==12) {
                    errorList.cipcInputInvalid = 'Please enter a valid CIPC registration number.';
                    // alert(errorList.cipcInputInvalid);
                }
                if(localStorage.getItem('businessRegistrationProofSars').trim()==='') {
                    errorList.sarsInputNoText = 'Please enter your business SARS registration number.';
                }
                if(localStorage.getItem('businessRegistrationProofSars').trim().length!==10) {
                    errorList.sarsInputInvalid = 'Please enter a valid SARS registration number.';
                    // alert(errorList.sarsInputInvalid);
                }
            
                // financials error
                if(Number(localStorage.getItem('annualSales')) === 0){
                    errorList.annualSalesError = "Please enter an amount for expected annual sales."
                }

                if(Number(localStorage.getItem('annualCostOfSales')) === 0){
                    errorList.annualCostOfSalesError = "Please enter an amount for expected annual cost of sales."
                }

                if(Number(localStorage.getItem('annualExpenses')) === 0){
                    errorList.annualExpensesError = "Please enter an amount for expected annual expenses."
                }

                switch (JSON.parse(localStorage.getItem('hasDebtHistory'))){
                    case true:
                        if(localStorage.getItem('debtType')===null) {
                            errorList.debtTypeError = 'Please select the type of debt you currently have.';
                        }
                        break;
                    default:
                        break;
                }
                break;
        }
        props.setErrors(errorList);
    }

    const handleClickNext = () => {
        validateInput();
        if (allowNextStage) {
            nextQuestions();
        } else {
            showErrors();
        }
    }

    return(
        <div className="nextPrev">
            <div className="nextPrevButtons">
                <div className={"active-application-button"} onClick={prevQuestions}>Previous</div>
                <div style={{marginLeft:15}} className={"active-application-button"} onClick={handleClickNext}>Next</div>
            </div>
        </div>
    );
}

export default Navigate;