import React from "react";

const Navigate = (props) => {

    const prevQuestions = () => {
        props.previousStep();
    }

    const nextQuestions = () => {
        props.nextStep();
    }

    let allowNextStage;

    const validateInput = () =>{
        if (props.selectedFinancialFileUpload){
            const issues = [!props.selected, !props.agreedTerms, (props.selectedFinancialFileUpload.size/(1024*1024*2)>1), !props.agreedPrivacy, !props.agreedCookies];
            issues.includes(true)?allowNextStage=false:allowNextStage=true;
        }else{
            const issues = [!props.agreedTerms, !props.agreedPrivacy, !props.agreedCookies];
            issues.includes(true)?allowNextStage=false:allowNextStage=true;
        }
    };

    const showErrors = () => {
        const errorList = {};
        if(props.financials) {
            props.selected?errorList.neededUploadsNotSelected = undefined :errorList.neededUploadsNotSelected = 'You need to upload the required files to complete the application.';
        }
        if(props.financials&&props.selectedFinancialFileUpload) {
            (Number(props.selectedFinancialFileUpload.size)/(1024*1024*2)>1)?errorList.fileUploadTooBig = 'Your file upload should be smaller than 2097KB':errorList.fileUploadTooBig=undefined;
        }
        if(!props.agreedTerms){
            errorList.termsConditionsNotAccepted = 'To submit this application you must accept the terms and conditions.';
        }
        if(!props.agreedPrivacy){
            errorList.privacyPolicyNotAccepted = 'To submit this application you must accept the privacy policy.';
        }
        if(!props.agreedCookies){
            errorList.cookiePolicyNotAccepted = 'To submit this application you must accept the cookie policy.';
        }
        props.setErrors(errorList);
    }

    const handleClickNext = () => {
        validateInput();
        if (allowNextStage) {
            nextQuestions();
        } else {
            showErrors();
        }
    }

    return(
        <div className="nextPrev">
            <div className="nextPrevButtons">
                <div className={"active-application-button"} onClick={prevQuestions}>Previous</div>
                <div style={{marginLeft:15}} className={"active-application-button"} onClick={handleClickNext}>Confirm</div>
            </div>
        </div>
    );
}

export default Navigate;